import React from 'react';
import WhatsappSrc from 'assets/img/whatsapp.jpg';
import { Wrapper, Image, Title, Subtitle } from './NotOpenMessage.styles';

const NotOpenMessage: React.FC = () => {
  return (
    <Wrapper>
      <Image src={WhatsappSrc} />
      <Title>Telefon musi by stale poczony</Title>
      <Subtitle>
        WhatsApp czy si z Twoim telefonem w celu synchronizacji wiadomoci. Aby zmniejszy uycie danych komrkowych, pocz telefon z sieci
        Wi-Fi.
      </Subtitle>
    </Wrapper>
  );
};

export default NotOpenMessage;
